<template>
    <view>
        <view style="padding: 0 20rpx 0 20rpx;background-color: #fff;">

            <view v-for="(item, index) in postData.content" :key="index">
                <user-post mode="home" :bodyData="item"></user-post>
            </view>
            <view v-if="isLoading">
                <view style="height: 100rpx;" v-if="postData.content == 0"></view>
                <view style="text-align: center;">
                    <image :src="$kt.file.byPath('image/icon/star.svg')" class="loading-icon" mode="widthFix">
                    </image>
                </view>
            </view>


            <view style="height: 10rpx;"></view>
            <view v-if="isEnd && postData.content != 0" style="text-align: center;color:#999;font-size: 28rpx;">
                <text>没有更多了~</text>
            </view>
            <view v-if="isMe && isStartFinish && postData.content == 0"
                @click="$kt.util.navTo('/pages/public/publish-post')"
                style="text-align: center;color:#222;font-size: 28rpx;">
                <view style="height: 60rpx;"></view>
                <view>
                    <image :src="$kt.file.byPath('image/icon/send.svg')" class="send-icon" mode="widthFix" />
                </view>
                <view class="btn">去发布第一条贴子吧~</view>
            </view>
            <view style="height: 10rpx;"></view>
        </view>
    </view>
</template>

<script>
export default {
    props: {
        isMe: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            isEnd: false,
            requestParam: {
                id: 0,
            },
            postData: {
                content: []
            },
            isLoading: false,
            isStartFinish: false,
            requestCount: 0
        }
    },
    created() {

        // 从本地缓存中获取数据
        var postDataMe = uni.getStorageSync('postDataMe');
        if (postDataMe) {
            this.postData.content = postDataMe;
        }

        this.getPostOfSelf();

    },
    // 初始化
    mounted() {
        uni.$on("userInfoScrolltolower",(data)=>{
            console.log(data,"=-=-=");
            if (data == "post") {
                this.getPostOfSelf();
            }
        })
    },

    methods: {
        getPostOfSelf() {
            if (this.isLoading || this.isEnd) {
                return null;
            }
            this.isLoading = true;

            if (this.requestCount == 0||this.postData.content.length==0) {
                this.requestParam.id = "9999999999999999";
            } else {
                this.requestParam.id = this.postData.content[this.postData.content.length - 1].id+"";
            }

            this.$kt.request.request({
                api: this.$api.business.ovo.post.getSelf,
                data: this.requestParam,
                stateSuccess: res => {
                    if (this.requestCount == 0) {
                        this.postData.content = [];
                    }

                    this.requestCount++;
                    var content = res.data;
                    if (content.length == 0) {
                        this.isEnd = true;
                        this.isLoading = false;
                        this.isStartFinish = true;
                        setTimeout(() => {
                            this.isEnd = false;
                        }, 30000);
                        return false;
                    }
                    this.postData.content = this.postData.content.concat(content);

                    this.isStartFinish = true;
                    setTimeout(() => {
                        this.isLoading = false;
                    }, 200);
                    // 将postData前100条数据缓存到本地
                    var postDataMe = this.postData.content.slice(0, 100);
                    uni.setStorageSync('postDataMe', postDataMe);
                }
            });
        }
    },
}
</script>

<style lang="scss" scoped>
.loading-icon {
    width: 120rpx;
    height: 120rpx;
    // filter: grayscale(100%);
    // 不停旋转的动画
    animation: rotate 3s linear infinite;
    filter: grayscale(100%);
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.send-icon {
    width: 150rpx;
    height: 150rpx;
    opacity: 0.8;
    filter: grayscale(100%);
    // 不停的左右摇摆
    animation: shake 3s linear infinite;
}

@keyframes shake {
    0% {
        transform: translateX(0) rotate(0deg);
    }

    50% {
        transform: translateX(-20rpx) rotate(20deg);
    }

    100% {
        transform: translateX(0);
    }
}

.btn {
    width: 300rpx;
    height: 80rpx;
    line-height: 80rpx;
    border-radius: 40rpx;
    color: #999;
    font-size: 28rpx;
    margin: 0 auto;
    margin-top: 20rpx;
}

.btn:active {
    box-shadow: 0 0 10rpx #f5f5f5 inset;
}
</style>